@mixin breakpoint($point) {
  @if $point == sm-only {
    @media (max-width: 42.4375em) {
      @content; } }

  @else if $point == sm-up {
    @media (min-width: 42.5em) {
      @content; } }

  @else if $point == md-only {
    @media (max-width: 56.1875em) {
      @content; } }

  @else if $point == md-up {
    @media (min-width: 56.25em) {
      @content; } }

  @else if $point == lg-only {
    @media (max-width: 81.5em) {
      @content; } }

  @else if $point == lg-up {
    @media (min-width: 81.1625em) {
      @content; } }

  @else {
    @media (min-width: ($point / 16) + 'em') {
      @content; } } }

