// == TYPOGRAPHY == // 

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400&display=swap');

body, input, select, textarea, li, p {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.0375em;
  line-height: 1.5;
}



// p {
//     margin: 0 0 2rem 0;
// }


strong, b {
  font-weight: 600;
}

i, em {
  font-style: italic;
}

h1, h2, h3, h4, h5 {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  margin-block: .8rem;
}

$sizeH: (
	1: 2rem,
	2: 1.7rem,
	3: 1.2rem,
	4: 1rem
); 

$sizeHSmall: (
	1: 2rem,
	2: 1.3rem,
	3: 1.1rem,
	4: 1rem
); 

@each $n, $size in $sizeH {
  h#{$n} {
      font-size: $size;
  }
}

@include breakpoint(sm-only){
  @each $n, $size in $sizeHSmall {
    h#{$n} {
        font-size: $size;
    }
  }
}


a {
  -moz-transition: border-bottom-color 0.25s ease-in-out;
  -webkit-transition: border-bottom-color 0.25s ease-in-out;
  -ms-transition: border-bottom-color 0.25s ease-in-out;
  transition: border-bottom-color 0.25s ease-in-out;
  text-decoration: none;
  border: none;

  &:hover {
    text-decoration: none;
  }
}

h1 a, h2 a, h3 a, h4 a, h5 a {
  color: inherit;
  text-decoration: none;
}



// GENERAL

.ty {
  font-family: "Lato", sans-serif;
  
  &-name, &-title, &-subtitle, &-item {
    font-family: "Lato", sans-serif;
    font-weight: 700;
    color: #000
  }

  &-title {
    font-size: 1.4rem;
  }

  &-subtitle {
    font-size: 1.1rem;
    margin-block: .5rem;
  }

  &-right {
    text-align: end;
  }

  &-themeLight {
    color: _palette(theme-light)
  }
}


// PROFILE 
.ty {
  &-name {
    font-size: 2rem;
    line-height: 2.2rem;
  }

  &-position {
    font-weight: 700;
  }

  &-email {
    font-weight: 700;
    font-style: italic;
  }

  &-prof, &-position {
    margin: 0 0 .05rem 0;
    font-size: .9rem;
    color: rgb(83, 83, 83)
  }

  &-lastUpdate {
    font-size: .8rem;
    color:rgb(161, 161, 161);
    text-align: right;
  }
}
